/*Header*/
.header {
  padding-top: 12px;
  background: #fff;
}

.header-icon {
  background: #d2e3c8;
  padding: 8px;
  border-radius: 4px;
  font-size: 15px;
  color: #4d6f52;
  cursor: pointer;
}
/* Button and color */
.ant-btn:hover {
  color: #4d6f52 !important;
  border-color: #4d6f52 !important;
}
.ant-btn-primary {
  background-color: #4d6f52;
}

.ant-btn-primary:hover {
  background-color: #173a1a !important;
  color: #fff !important;
}
.primary--color {
  color: #4d6f52 !important;
}
.primary--light--color {
  color: #739072 !important;
}
.secondary--color {
  color: #86a789 !important;
}
.light--color {
  color: #d2e3c8 !important;
}
.grey--color {
  color: #b0b0b0 !important;
}

/*Content*/
.content {
  margin: 24px 16px;
  padding: 20px;
}

/* Main app */

/* button */

.trigger-button:hover .anticon {
  color: #060606;
}

.trigger-button .anticon {
  color: #fff;
}
.trigger-button {
  font-size: 16px;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 10px;
  left: 10px;
  background-color: #4d6f52;
}

/* Sider */
.sider {
  height: 100vh;
  position: sticky !important;
  left: 0;
  top: 0;
  bottom: 0;
}

/* Logo */
.logo {
  color: #4d6f52;
  margin: 40px 0;
  font-size: 1.5rem;
}

.menu-bar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-weight: 500;
}
.ant-menu-item-selected {
  background-color: #4d6f52 !important;
  color: #fff !important;
}

/* plant list */

.plant-card {
  flex: 1;
  height: 250px;
  text-align: center;
}

/* Seller list  */

.top-seller,
.featured-seller {
  flex: 1;
}

.divider {
  border: 2px solid green;
  height: 50px;
}

.card {
 background-image: linear-gradient(to bottom, #4c7f79, #87e8de);
}